class HandleBanners {
  constructor(banners1, banners2, banners3) {
    this.banners1 = banners1;
    this.banners2 = banners2;
    this.banners3 = banners3;
    this.getBanners = this.getBanners.bind(this);
  }

  getBanners(year = '2020') {
    if (year === '2020') {
      return this.banners1;
    } if (year === '2021') {
      return this.banners2;
    } if (year === '2022') {
      return this.banners3;
    }
    return true;
  }
}

export default HandleBanners;
